import styled from "styled-components";

export const DashboardContainer = styled.div`
  height: 100%;
  min-height: 100vh;
`;
export const DashboardWrapper = styled.div`
  padding: 24px;
`;

// Dashboard data
export const DashboardData = styled.div`
  display: flex;
  height: 60vh;
  @media (max-width: 1072px) {
    flex-direction: column;
  }
`;

export const DashboardDataChart = styled.div`
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  padding: 20px;
  width: 50%;
  height: 100%;
  background-color: #e7dbce;
  border-radius: 8px;
  @media (max-width: 1072px) {
    width: 100%;
    margin-bottom: 10px;
  }
  @media (max-width: 724px) {
    display: none;
  }
`;
export const DashboardDataReports = styled.div`
  display: flex;
  flex-direction: column;
  width: 50%;
  height: 100%;
  margin-left: 10px;
  @media (max-width: 1072px) {
    flex-direction: row;
    justify-content: center;
    width: 100%;
  }
  @media (max-width: 724px) {
    margin-top: 100px;
    flex-direction: column;
    width: 100%;
    height: 100%;
  }
`;
export const ReportRow = styled.div`
  display: flex;
  width: 100%;
  height: 50%;
  @media (max-width: 1072px) {
    flex-direction: column;
    height: 100%;
  }
  @media (max-width: 724px) {
    width: 100%;
    height: 100%;
  }
`;
export const ReportData = styled.div`
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  width: 50%;
  margin: 0 10px;
  background-color: #fff;
  border-radius: 8px;
  position: relative;

  @media (max-width: 1072px) {
    width: 90%;
    height: 100%;
    margin: 10px 0;
  }
`;
export const ReportDataContent = styled.div`
  padding: 24px 24px;
`;
export const ContentTitle = styled.p``;
export const ContentNumber = styled.p`
  font-size: 28px;
  display: block;
  margin: 20px 0;
`;
export const ContentSub = styled.p`
  font-size: 14px;
`;
export const ContentIcon = styled.div`
  position: absolute;
  top: 10%;
  right: 5%;
  font-size: 28px;
  background-color: #f0f0f0;
  color: black;
  padding: 8px 8px;
  border-radius: 8px;
  @media (max-width: 1000px) {
    font-size: 20px;
  }
`;

// Dashboard table
export const DashboardTable = styled.div``;
