import { createReducer } from "@reduxjs/toolkit";

import { REQUEST, SUCCESS, FAIL, USER_ACTION } from "../constants";

const initialState = {
  userList: {
    data: [],
    meta: {},
    loading: false,
    error: null,
  },
  updateUserInfoData: {
    loading: false,
    error: null,
  },

  userInfo: {
    data: {},
    userCartList: {
      data: localStorage.getItem("cartItems")
        ? JSON.parse(localStorage.getItem("cartItems"))
        : [],
      totalAmount: 0,
      totalCount: 0,
      loading: false,
      error: null,
    },
    loading: false,
    error: null,
  },
  loginData: {
    loading: false,
    error: null,
  },
  registerData: {
    loading: false,
    error: null,
  },
};

const userReducer = createReducer(initialState, {
  [REQUEST(USER_ACTION.LOGIN)]: (state) => {
    return {
      ...state,
      loginData: {
        ...state.loginData,
        loading: true,
        error: null,
      },
    };
  },
  [SUCCESS(USER_ACTION.LOGIN)]: (state, action) => {
    const { data } = action.payload;
    return {
      ...state,
      userInfo: {
        ...state.userInfo,
        data: data,
      },
      loginData: {
        ...state.loginData,
        loading: false,
      },
    };
  },
  [FAIL(USER_ACTION.LOGIN)]: (state, action) => {
    const { error } = action.payload;
    return {
      ...state,
      loginData: {
        ...state.loginData,
        loading: false,
        error: error,
      },
    };
  },
  [REQUEST(USER_ACTION.UPDATE_USER)]: (state, action) => {
    return {
      ...state,
      updateUserInfoData: {
        ...state.updateUserInfoData,
        loading: true,
        error: null,
      },
    };
  },
  [SUCCESS(USER_ACTION.UPDATE_USER)]: (state, action) => {
    return {
      ...state,
      updateUserInfoData: {
        ...state.updateUserInfoData,
        loading: false,
      },
    };
  },
  [FAIL(USER_ACTION.UPDATE_USER)]: (state, action) => {
    const { error } = action.payload;
    return {
      ...state,
      updateUserInfoData: {
        ...state.updateUserInfoData,
        loading: false,
        error: error,
      },
    };
  },
  [REQUEST(USER_ACTION.GET_USER_INFO)]: (state) => {
    return {
      ...state,
      userInfo: {
        ...state.userInfo,
        loading: true,
        error: null,
      },
    };
  },
  [SUCCESS(USER_ACTION.GET_USER_INFO)]: (state, action) => {
    const { data } = action.payload;
    return {
      ...state,
      userInfo: {
        ...state.userInfo,
        data: data,
        loading: false,
      },
    };
  },
  [FAIL(USER_ACTION.GET_USER_INFO)]: (state, action) => {
    const { error } = action.payload;
    return {
      ...state,
      userInfo: {
        ...state.userInfo,
        loading: false,
        error: error,
      },
    };
  },
  //
  [REQUEST(USER_ACTION.GET_USER_LIST)]: (state) => {
    return {
      ...state,
      userList: {
        ...state.userList,
        loading: true,
        error: null,
      },
    };
  },
  [SUCCESS(USER_ACTION.GET_USER_LIST)]: (state, action) => {
    const { data, meta } = action.payload;
    return {
      ...state,
      userList: {
        ...state.userList,
        data: data,
        meta: meta,
        loading: false,
      },
    };
  },
  [FAIL(USER_ACTION.GET_USER_LIST)]: (state, action) => {
    const { error } = action.payload;
    return {
      ...state,
      userList: {
        ...state.userList,
        loading: false,
        error: error,
      },
    };
  },

  //
  [REQUEST(USER_ACTION.REGISTER)]: (state) => {
    return {
      ...state,
      registerData: {
        ...state.registerData,
        loading: true,
        error: null,
      },
    };
  },
  [SUCCESS(USER_ACTION.REGISTER)]: (state, action) => {
    return {
      ...state,
      registerData: {
        ...state.registerData,
        loading: false,
      },
    };
  },
  [FAIL(USER_ACTION.REGISTER)]: (state, action) => {
    const { error } = action.payload;
    return {
      ...state,
      registerData: {
        ...state.registerData,
        loading: false,
        error: error,
      },
    };
  },
  [REQUEST(USER_ACTION.LOGOUT)]: (state) => {
    localStorage.removeItem("accessToken");
    return {
      ...state,
      userInfo: {
        data: {},
        loading: false,
        error: null,
      },
    };
  },
});

export default userReducer;
